import React from 'react'

const SearchBox = ({serchfield,searchChange}) => {
    return(
        <div className= 'pa2'>
         <input
          className ='pa3 bs b--green bg-lightest-blue' 
          type='search' placeholder='search robots' 
          onChange={searchChange}
          />
          
        </div>
    );

}
export default SearchBox;